// The Vue build version to load with the `import` command
// (runtime-only or standalone) has been set in webpack.base.conf with an alias.
import Vue from 'vue'
import App from './App'
import router from './router'

//第三方
import swiper from 'swiper'
import axios from 'axios'
import ElementUI from 'element-ui'
import 'element-ui/lib/theme-chalk/index.css'
//公共样式
import '@/assets/css/base.css'

//头部组件
import MyHeader from '@/components/MyHeader'
//尾部组件
import MyFooter from '@/components/myFooter'
//请求路径
import https from './api/user.js'
import img_http from './api/user.js'
Vue.prototype.url = https
Vue.prototype.img_url = img_http

Vue.config.productionTip = false

//全局组件
Vue.use(ElementUI)
Vue.component('my-header', MyHeader)
Vue.component('my-footer', MyFooter)
//时间戳转换
Vue.filter("formatTime",function(value,type){
	function addZero(val){
		if(val < 10){
		  return "0" +val;
		}else{
		  return val;
		}
	};
	var dataTime="";
	var data = new Date();  
	data.setTime(value*1000); 
	var year   =  data.getFullYear();  
	var month  =  addZero(data.getMonth() + 1);  
	var day    =  addZero(data.getDate()); 
	var hour   =  addZero(data.getHours());
	var minute =  addZero(data.getMinutes());
	var second =  addZero(data.getSeconds());
	var wook = '';
    switch (data.getDay()) {
        case 0:
            wook = '周日';
            break;
        case 1:
            wook = '周一';
            break;
        case 2:
            wook = '周二';
            break;
        case 3:
            wook = '周三';
            break;
        case 4:
            wook = '周四';
            break;
        case 5:
            wook = '周五';
            break;
        case 6:
            wook = '周六';
            break;
    }
	if(type == "month"){
		dataTime = month
	}else if(type == "date"){
		dataTime = day
	}else if(type == "YMD"){
	dataTime =  year + "年"+ month + "月" + day + "日";
	}else if(type == "YMDHM"){
	dataTime = year + "年"+month + "月" + day + "日（"+ wook + "）" +hour+ ":"+minute;
	}else if(type == "YMDHM-"){
	dataTime = year + "-"+month + "-" + day + " " +hour+ ":"+minute;
	}else if(type == "HM"){
	dataTime = hour+":" + minute;
	}
	return dataTime;//将格式化后的字符串输出到前端显示
});
/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  components: { App },
  template: '<App/>'
})



