<template>
	<div class="museum">
		<my-header style='position: relative;'></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--main开始-->
		<div class="mainbox center" id="mainbox">
			<router-link tag="div" :to="{name:'mus_second',params:{id:51,activename:'first'}}">
				<a href="museum/mus_second.html">
					<i></i>
					<p>珍藏瑰宝</p>
					<img src="../assets/img/ltgbimg.jpg" />
				</a>
			</router-link>
			<router-link tag="div" :to="{name:'mus_second',params:{id:52,activename:'second'}}">
				<a href="museum/mus_second.html">
					<i></i>
					<p>书画</p>
					<img src="../assets/img/shimg.jpg" />
				</a>
			</router-link>
			<router-link tag="div" :to="{name:'mus_second',params:{id:53,activename:'third'}}">
				<a href="museum/mus_second.html">
					<i></i>
					<p>工艺品</p>
					<img src="../assets/img/gypimg.jpg" />
				</a>
			</router-link>
			<router-link tag="div" :to="{name:'mus_second',params:{id:54,activename:'fourth'}}">
				<a href="museum/mus_second.html">
					<i></i>
					<p>名家题字</p>
					<img src="../assets/img/mingimg.jpg" />
				</a>
			</router-link>
		</div>
		<!--main结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
	</div>
</template>
<script type="text/javascript">
	export default {
		name: 'museum',
		data(){
			return{
				
			}
		}
	}
</script>
<style scoped>
.main{
	width: 100%;
	background:#ececec;
}
/*================banner style=============*/
.banner{
	width: 100%;
	height:600px;
	background: url(../assets/img/museum_banner.jpg) no-repeat center;
	background-size:cover;
}
/*===========mainbox style====================*/
.mainbox{
	width: 1200px;
	margin: 0 auto 130px;
}
.mainbox > div{
	width: 212px;
	display: flex;
	flex-direction: column;
	justify-content: space-around;
	overflow: hidden;
}
.mainbox > div i{
	display: block;
	width: 86px;
	height: 86px;
	margin:0 auto;
	overflow: hidden;
}

.mainbox > div:nth-of-type(1) i{
	background: url("../assets/img/collectIndexImg.png") no-repeat 0px 0px;
}
.mainbox > div:nth-of-type(2) i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -86px 0px;
}
.mainbox > div:nth-of-type(3) i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -258px 0px;
}
.mainbox > div:nth-of-type(4) i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -430px 0px;
}

.mainbox > div:nth-of-type(1){
	margin-top: 100px;
	transition:.3s;
}
.mainbox > div:nth-of-type(1):hover{
	margin-top: 70px;
}
.mainbox > div:nth-of-type(1):hover i{
	background: url("../assets/img/collectIndexImg.png") no-repeat 0px -86px;
}

.mainbox > div:nth-of-type(2){
	margin-top: 0;
	transition:.3s;
}
.mainbox > div:nth-of-type(2):hover{
	margin-top: -30px;
}
.mainbox > div:nth-of-type(2):hover i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -86px -86px;
}

.mainbox > div:nth-of-type(3){
	margin-top: 100px;
	transition:.3s;
}
.mainbox > div:nth-of-type(3):hover{
	margin-top: 70px;
}
.mainbox > div:nth-of-type(3):hover i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -258px -86px;
}

.mainbox > div:nth-of-type(4){
	margin-top: 0px;
	transition:.3s;
}
.mainbox > div:nth-of-type(4):hover{
	margin-top: -30px;
}
.mainbox > div:nth-of-type(4):hover i{
	background: url("../assets/img/collectIndexImg.png") no-repeat -430px -86px;
}
.mainbox > div img{
	display: block;
	width: 100%;
	margin: 0 auto;
}
.mainbox > div p{
	text-align: center;
	margin: 15px 0;
	font-size: 20px;
}
.hr{
	width: 100%;
	height: 5px;
	background: #fff;
}
</style>


