<template>
	<div class="activitys">
		<my-header style="position: relative;"></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--main开始-->
		<<div class="mainbox">
			<ul class="activity_list">
				<router-link tag="li" v-for="item in activity_list" :key="item.id" :to="'/act_second/'+item.id">
					<i>
							<img :src="img_http+ item.img_url"/>
						</i>
					<div>
						<h2 class="ellipsis">{{item.title}}</h2>
						<p class="activity_time">活动时间：{{item.start_time | formatTime('YMDHM')}}-{{item.end_time | formatTime('HM')}}</p>
						<p class="apply_time">报名时间：{{item.apply_start_time | formatTime('YMDHM-')}} - {{item.apply_end_time | formatTime('YMDHM-')}}</p>
						<p class="activity_address">{{ item.address }}</p>
						<p class="activity_text ellipsis">{{ item.desc }}</p>
						<p class="activity_people" v-if="item.apply_end_time > currentTime">已预约<span class="number">100</span>人&nbsp;&nbsp;<span class="inIng">正在预约</span></p>
						<a href="activity/act_second.html" v-if="item.apply_end_time > currentTime">立即预约</a>
						<a v-if="!(item.apply_end_time > currentTime)" class="aActive">预约已结束</a>
					</div>
				</router-link>
			</ul>
			<!--分页-->
			<el-pagination class="activitypag center" background layout="prev, pager, next" :page-count="pages" :current-page="currentPage" @current-change="currentChange">
			</el-pagination>
	</div>
	<!--main结束-->
	<my-footer style="position: relative;"></my-footer>
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	export default {
		name: 'activitys',
		data() {
			return {
				activity_list: [],
				pages: 5,
				currentPage: 1,
				currentTime: null,
				https:this.url.https,
				img_http:this.img_url.img_http,
			}
		},
		mounted() {
			this.currentTime = parseInt(new Date().getTime() / 1000);
			var that = this;
			axios.get(that.https+'/pavilion/activity', {
				params:{
						page: that.currentPage
					}
				}).then(
				function(data) {
					var data = data.data
					console.log(data);
					that.activity_list = data.list;
					//                  that.pages = data.count;
				})
		},
		methods: {
			currentChange(val) {
				this.currentPage = val;
				var that = this;
				//				console.log(val);
				axios.get(that.https+'/pavilion/activity', {
						params:{
							page: val
						}
					}).then(
					function(data) {
						var data = data.data
						//                  console.log(data);
						that.activity_list = data.list
					})
			}
		}
	}
</script>
<style type="text/css">
	.main {
		width: 100%;
	}
	/*================banner style=============*/
	
	.banner {
		width: 100%;
		height: 600px;
		background: url(../assets/img/activity_banner.jpg) no-repeat center;
		background-size: cover;
	}
	/*================main style==============*/
	
	.mainbox {
		width: 1200px;
		margin: 0 auto 60px;
	}
	
	.mainbox .activity_list {
		padding-top: 38px;
	}
	
	.mainbox .activity_list li {
		width: 80%;
		padding: 42px 10%;
		margin: 0 auto;
		display: flex;
		align-content: center;
		border-bottom: 1px solid #d9d9d9;
	}
	
	.mainbox .activity_list li:last-of-type {
		border-bottom: 0;
	}
	
	.mainbox .activity_list li i {
		width: 383px;
		height: 258px;
		display: block;
		overflow: hidden;
	}
	
	.mainbox .activity_list li div {
		width: calc(100% - 429px);
		margin-left: 46px;
		font-size: 16px;
	}
	
	.mainbox .activity_list li h2 {
		margin: 5px 0;
		font-size: 20px;
	}
	
	.mainbox .activity_list li p {
		line-height: 36px;
	}
	
	.mainbox .activity_list li .activity_people {
		color: #ff9a01;
	}
	
	.mainbox .activity_list li .inIng {
		display: initial;
		color: #b4d889;
	}
	
	.mainbox .activity_list li a {
		display: block;
		width: 136px;
		height: 51px;
		line-height: 51px;
		text-align: center;
		background: #ff9a01;
		color: #fff;
		border-radius: 10px;
	}
	
	.mainbox .activity_list li .aActive {
		background: #aaaaaa;
		cursor: not-allowed;
	}
	
	.el-pagination {
		margin-top: 20px;
	}
	
	.activitypag.center {
		justify-content: flex-end;
	}
	
	.el-pagination button,
	.el-pagination li {
		width: 64px;
		height: 45px;
		line-height: 45px;
		text-align: center;
		margin: 0 5px;
		background: #e4e4e4;
		cursor: pointer;
		font-size: 18px;
	}
	
	.el-pager li.btn-quicknext,
	.el-pager li.btn-quickprev {
		line-height: 45px;
	}
	
	.el-pagination.is-background .el-pager li:not(.disabled).active {
		background: #f5ac37;
		color: #fff;
	}
	
	.el-pagination.is-background .el-pager li:not(.disabled):hover {
		background: #f5ac37;
		color: #fff;
	}
</style>


