<template>
	<div class="con_sec">
		<my-header style='position: relative;'></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--选项卡开始-->
		<el-tabs v-model="activeName" :before-leave="myclick">
			<el-tab-pane label="场馆介绍" name="first" class="introduce">
				
			</el-tab-pane>
			<el-tab-pane label="最新动态" name="second" class="dynamic">
				<div class="list_xq">
					<div>
						<h2>{{list.title}}</h2>
						<p>时间：<time>{{list.created_at | totime(list.created_at)}}</time> <span>浏览次数：{{list.view_count}}</span></p>
						<article v-html="list.content">
							{{list.content}}
						</article>
					</div>
				</div>
			</el-tab-pane>
			<el-tab-pane label="开放信息" name="third" class="dispark_mes">
				
			</el-tab-pane>
			<el-tab-pane label="大事记" name="fourth" class="big_shi">
				
			</el-tab-pane>
		</el-tabs>
		<!--选项卡结束-->
		<my-footer style="position: relative;"></my-footer>
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	export default {
		name: 'con_sec',
		data() {
			return {
				activeName: 'second',
				list:[],
				id:this.$route.params.id,
				https:this.url.https,
			}
		},
		mounted() {
			var that = this;
			console.log(that.id)
			axios.get(that.https+'/pavilion/news-detail?id=' + that.id).then(
			function(data) {
				var data = data.data
//				console.log(data);
				that.list = data;
			});
			
		},
		methods:{
			myclick(activeName, oldActiveName){
				this.$router.push({name: 'consult', params: {activeName: activeName}})
			}
		}
	}
</script>
<style scoped>
	/*================banner style=============*/
	
	.banner {
		width: 100%;
		height: 600px;
		background: url(../assets/img/consult_banner.jpg) no-repeat center;
		background-size: cover;
	}
	/*=========== el-tabs style ===========*/
	
	.el-tabs__header {
		height: 130px;
		border-bottom: 4px solid #f2f0f3;
		background: #f4f4f4;
		margin: 0;
	}
	
	.el-tabs__nav {
		width: 1200px;
		margin: 0 auto;
		float: none;
		height: 134px;
	}
	
	.el-tabs__item {
		height: 130px;
		width: 25%;
		font-size: 26px;
		padding: 0;
		line-height: 130px;
		color: #101010;
		text-align: center;
	}
	
	.el-tabs__item.is-active {
		color: #E51C23;
	}
	
	.el-tabs__item:hover {
		color: #101010;
	}
	.el-tabs__item.is-active:hover{
		color: #E51C23;
	}
	
	.el-tabs__active-bar {
		background-color: #E51C23;
		left: 5px;
		height: 4px;
	}
	
	.el-tabs__content {
		width: 1200px;
		padding: 66px 0;
		margin: 0 auto;
	}
	
	/*动态详情*/
.list_xq{
	width:100%;
}
.list_xq h2{
	line-height: 40px;
}
.list_xq p{
	line-height: 25px;
	color: #888c90;
}
.list_xq article{
	margin: 20px 0;
	line-height: 25px;
}
.list_xq article img {
	display: block;
	width:70%;
	margin:20px auto;
}

</style>


