<template>
	<div class="act_sec">
		<my-header style="position: relative;"></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--main开始-->
		<div class="mainbox">
			<div class="box_one center">
				<i>
						<img :src="img_http+act_list.img_url"/>
					</i>
				<div>
					<h2 class="title ellipsis">{{ act_list.title }}</h2>
					<p class="activity_time">活动时间：{{act_list.start_time | formatTime('YMDHM')}}-{{act_list.end_time | formatTime('HM')}}</p>
						<p class="apply_time">报名时间：{{act_list.apply_start_time | formatTime('YMDHM-')}} - {{act_list.apply_end_time | formatTime('YMDHM-')}}</p>
					<p class="activity_address">活动地点：{{act_list.address}}</p>
					<p class="activity_text ellipsis">{{ act_list.desc}}</p>
					<p class="activity_people"><span class="inIng">正在预约</span></p>
				</div>
			</div>
			<div class="box_two">
				<h4>我要报名：</h4>
				<p>微信扫描下方二维码即可在线预约</p>
				<i>
						<img :src="'http://qr.liantu.com/api.php?text=' + act_list.apply_url"/>
					</i>
			</div>
			<div class="box_three">
				<h4>详情介绍：</h4>
				<article v-html="act_list.content">
					{{ act_list.content }}
				</article>
			</div>
		</div>
		<!--main结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	export default{
		name:'act_sec',
		data(){
			return{
				id:this.$route.params.id,
				act_list:'',
				https:this.url.https,
				img_http:this.img_url.img_http
			}
		},
		mounted(){
			 axios.get(this.https+'/pavilion/activity-detail?id=' + this.id).then((data)=>{
			 	var data = data.data
//			 	console.log(data)
				this.act_list = data;
			 })
			 /*$.get(this.https+'/pavilion/activity-detail?id=' + this.id,(data)=>{
//			 	console.log(data)
				this.act_list = data;
			 })*/
		}
	}
</script>
<style type="text/css" scoped>
.main{
	width: 100%;
}
/*================banner style=============*/
.banner{
	width: 100%;
	height: 600px;
	background: url(../../assets/img/activity_banner.jpg) no-repeat center;
	background-size:cover;
}
/*=================main style==============*/
.mainbox{
	width: 1200px;
	margin: 0 auto;
	padding-top: 43px;
}
.mainbox > div{
	border-bottom: 1px solid #EEEEEE;
}
.mainbox > div:last-of-type{
	border-bottom: 0;
}
.mainbox .box_one{
	padding: 38px 60px;
}
.mainbox .box_one.center{
	align-items: flex-start;
}
.mainbox .box_one i{
	width: 383px;
	height: 258px;
	display: block;
	overflow: hidden;
}
.mainbox .box_one div{
	width: calc(100% - 383px);
	margin-left: 36px;
}
.mainbox .box_one h2{
	margin: 10px 0;
	font-size: 20px;
	font-weight: normal;
}
.mainbox .box_one p{
	margin: 14px 0;
	line-height: 18px;
}
.mainbox .box_one .inIng{
	display: initial;
	color: #b4d889;
}
.mainbox .box_two{
	text-align: center;
	padding: 40px 60px;
}
.mainbox h4{
	text-align: left;
	font-size: 20px;
}
.mainbox .box_two p{
	padding-top:55px;
}
.mainbox .box_two i{
	padding-top: 43px;
	width: 190px;
	height:190px;
	display: inline-block;
	overflow: hidden;
}
.mainbox .box_two i img{
	display: block;
	width: 99%;
	margin: 0 auto;
}
.mainbox .box_three{
	padding-top: 60px;
}
.mainbox .box_three article{
	width:95%;
	margin:0 auto;
	padding: 47px 0;
	line-height: 30px;
}
.mainbox .box_three article img{
	width:80%;
	margin:30px auto;
}

</style>


