<template>
	<div class="wenCreative">
		<my-header style="position: relative;"></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--main开始-->
		<div class="mainbox">
			<h3>全部分类</h3>
			<ul class="center product">
				<li>
					<i>
							<img src="../assets/img/wen_bai.jpg"/>
						</i>
					<h2>百印祥福</h2>
				</li>
				<li>
					<i>
							<img src="../assets/img/wen_3.jpg"/>
						</i>
					<h2>三十三观音</h2>
				</li>
			</ul>
			<div class="product_list">
				<ul>
					<li>
						<!--<p>镇馆之宝</p>
							<i>
								<img src="../assets/img/二屏.png"/>
							</i>-->
						<i>
								<img src="../assets/img/wen_bai_1.jpg">
							</i>
					</li>
					<li>
						<i>
								<img src="../assets/img/wen_3-1.jpg"/>
							</i>
					</li>
				</ul>
			</div>
		</div>
		<!--main结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
	</div>
</template>
<script type="text/javascript">
	export default {
		name: 'wenCreative'
	}
</script>
<style type="text/css">
.main{
	width: 100%;
}
/*================banner style=============*/
.banner{
	width: 100%;
	height: 600px;
	background: url(../assets/img/wenCreative_banner.jpg) no-repeat center;
	background-size:cover;
}
/*================main style==============*/
.mainbox{
	width: 1200px;
	margin: 0 auto;
}
.mainbox h3{
	width: 100%;
	padding-top: 58px;
	text-align: center;
	font-size: 28px;
	position: relative;
}
.mainbox h3:after{
	content: '';
	height: 10px;
	width: 0px;
	border-top: 16px solid #bfbfbf;
	border-left: 10px solid transparent;
	border-right: 10px solid transparent;
	position: absolute;
	bottom: -35%;
	left: 50%;
	transform: translateX(-50%);
}
.mainbox .product{
	margin: 66px 0 0;
	border-bottom: 1px solid #dfdfdf;
}
.mainbox .product.center{
	justify-content: space-around;
}
.mainbox .product li i{
	display: flex;
	width: 280px;
	height: 280px;
	border-radius: 50%;
	overflow: hidden;
	justify-content: center;
	align-content: center;
}
.mainbox .product li h2{
	font-size: 26px;
	line-height: 100px;
	text-align: center;
	font-weight: normal;
}
.mainbox .product_list{
	padding-top: 4px;
	padding-bottom: 260px;
}
.mainbox .product_list li{
	padding:40px 0;
	display: flex;
	justify-content: center;
}
/*.mainbox .product_list li:nth-of-type(2n){*/
	/*flex-direction: row-reverse;*/
/*}*/
.mainbox .product_list li p{
	width: 25px;
	word-wrap: break-word;
	font-size: 18px;
	margin: 0 50px;
}
.mainbox .product_list li i{
	display: block;
	width: 70%;
	overflow: hidden;
}

</style>


