import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/pages/Home'
import Activity from '@/pages/activity'
import consult from '@/pages/consult'
import con_sec from '@/pages/con_second'
import museum from '@/pages/museum'
import wenCreative from '@/pages/wenCreative'
import act_sec from '@/pages/activity/act_second'
import mus_sec from '@/pages/museum/mus_second'
import log_in from '@/pages/log_in'
import yu_yue from '@/pages/yu_yue'
import show_Img from '@/pages/show_Img'

Vue.use(Router)

export default new Router({
	base:"/dist",
	routes: [{
			path: '/',
			name: 'home',
			component: Home
		},
		{
			path: '/consult',
			name: 'consult',
			component: consult
		},
		{
			path: '/con_sec/:id',
			name: 'con_sec',
			component: con_sec
		},
		{
			path: '/museum',
			name: 'museum',
			component: museum,
		},
		{
			path: '/mus_second/:id',
			name: 'mus_second',
			component: mus_sec,
		},
		{
			path: '/wenCreative',
			name: 'wenCreative',
			component: wenCreative
		},
		{
			path: '/activity',
			name: 'activity',
			component: Activity,
		},
		{
			path: '/act_second/:id',
			name: 'act_second',
			component: act_sec
		},
		{
			path: '/log_in',
			name: 'log_in',
			component: log_in
		},
		{
			path: '/yu_yue',
			name: 'yu_yue',
			component: yu_yue
		},
		{
			path: '/show_Img',
			name: 'show_Img',
			component: show_Img
		}
	]
})


