<template>
	<div class="MyHeader header center" id="header">
		<div class="header_left">
			<h1><img src="../assets/img/佛缘之路logo.png"/></h1>
		</div>
		<nav class="header_right">
			<ul class="center">
				<li @click.stop="home()" :class="{active: selected == 'home'}">
					<router-link to='/'>首页</router-link>
				</li>
				<li @click.stop="consult()" :class="{active: selected == 'consult'}">
					<router-link to='/consult'>资讯</router-link>
				</li>
				<li @click.stop="museum()" :class="{active: selected == 'museum' || selected == 'mus_second'}">
					<router-link to='/museum'>馆藏</router-link>
				</li>
				<li @click.stop="wenCreative()" :class="{active: selected == 'wenCreative'}">
					<router-link to='/wenCreative'>文创</router-link>
				</li>
				<li @click.stop="activity()" :class="{active: selected == 'activity' || selected == 'act_second'}">
					<router-link to='/activity'>活动</router-link>
				</li>
			</ul>
		</nav>
	</div>
</template>

<script>
	export default {
		name: 'MyHeader',
		data() {
			return {
				selected: this.$route.name
			}
		},
		watch: {
			$route(val) {
				this.init()
			}
		},
		methods: {
			init() {
				if(this.$route.name === 'home' || this.$route.name === 'consult' || this.$route.name === 'museum' || this.$route.name === 'wenCreative' || this.$route.name === 'activity') {
					this.selected = this.$route.name
				}
			},
			home() {
				this.$router.push({
					name: 'home'
				})
				this.selected = this.$route.name
			},
			consult() {
				this.$router.push({
					name: 'consult'
				})
				this.selected = this.$route.name
			},
			museum() {
				this.$router.push({
					name: 'museum'
				})
				this.selected = this.$route.name
			},
			wenCreative() {
				this.$router.push({
					name: 'wenCreative'
				})
				this.selected = this.$route.name
			},
			activity() {
				this.$router.push({
					name: 'activity'
				})
				this.selected = this.$route.name
			},
		}
	}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style>
	.header {
		width: 100%;
		height: 110px;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 999;
		background: #fff;
	}
	
	.header_left {
		padding-left: 105px;
	}
	
	.header_left h1 {
		width: 400px;
		/*height: 82px;*/
		overflow: hidden;
	}
	
	.header_right {
		padding-right: 86px;
	}
	
	.header_right li {
		padding: 0 38px;
		color: #101010;
		font-size: 26px;
		border-bottom-color: transparent;
		border-bottom-style: solid;
		border-bottom-width: 2px;
		transition: .3s;
	}
	
	.header_right a {
		color: #101010;
		padding-bottom: 5px;
	}
	
	.header_right .active a {
		color: #ff0300;
		border-bottom-color: #ff0300;
		border-bottom-style: solid;
		border-bottom-width: 2px;
	}
	
	@media screen and (max-width: 1366px) {
		.header_right {
			padding-right: 36px;
		}
		.header_right li {
			padding: 0 30px;
		}
	}
</style>


