<template>
	<div class="mus_sec">
		<my-header style='position: relative;'></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--选项卡开始-->
		<el-tabs v-model="activeName" :before-leave="myclick">
			<el-tab-pane label="珍藏瑰宝" name="first" class="introduce">
				<div class="list clearfix">
					<ul>
						<li v-for="(item,index) in imgs" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs1" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs2" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs3" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
				</div>
			</el-tab-pane>
			<el-tab-pane label="书画" name="second" class="dynamic">
				<div class="list">
					<ul>
						<li v-for="(item,index) in imgs" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs1" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs2" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs3" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
				</div>
			</el-tab-pane>
			<el-tab-pane label="工艺品" name="third" class="dispark_mes">
				<div class="list">
					<ul>
						<li v-for="(item,index) in imgs" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs1" @click="list_xq(item.id)" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs2" @click="list_xq(item.id)" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs3" @click="list_xq(item.id)" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
				</div>
			</el-tab-pane>
			<el-tab-pane label="名家题字" name="fourth" class="big_shi">
				<div class="list">
					<ul>
						<li v-for="(item,index) in imgs" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs1" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs2" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs3" data-scroll-reveal="enter" @click="list_xq(item.id)"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
				</div>
			</el-tab-pane>
		</el-tabs>
		<!--选项卡结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
		<!--图片详情弹框开始-->
		<div v-if="!show" class="img_alert" id="img_alert">
			<i id="close" title="关闭" @click="close()">×</i>
			<div class="img_introduce">
				<h3>{{img_xq.title}}</h3>
				<p>浏览次数：<span class="img_number">{{img_xq.view_count}}</span>次</p>
				<i><img class="img_img" :src="img_http+img_xq.img_url" alt="img_xq.title"></i>
				<p class="img_text" v-html="img_xq.content">
					{{img_xq.content}}
				</p>
			</div>
		</div>
		<!--图片详情弹框结束-->
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	import '../../assets/js/scrollReveal.js'
	if(!(/msie [6|7|8|9]/i.test(navigator.userAgent))) {
		(function() {
			window.scrollReveal = new scrollReveal({
				reset: true
			});
		})();
	};

	export default {
		name: 'mus_sec',
		data() {
			return {
				https:this.url.https,
				img_http:this.img_url.img_http,
				activeName: 'first',
				show: true,
				id: this.$route.params.id,
				imgs: [],
				imgs1: [],
				imgs2: [],
				imgs3: [],
				img_xq:''
			}
		},
		methods: {
			getwz() {
				var that = this;
				that.imgs = [];
				that.imgs1 = [];
				that.imgs2 = [];
				that.imgs3 = [];
				axios.get(that.https+"/pavilion/news?c=" + that.id).then(function(data) {
					var data = data.data
					that.activeName = that.$route.params.activename;
					var bili = parseInt(data.list.length / 4);
					if(bili == 0) {
						bili = 1
					} else {
						bili = bili
					}
					var imgarr = [];
					for(var i = 0, len = data.list.length; i < len; i++) {
						imgarr.push(data.list[i])
					}
					that.imgs = imgarr.slice(0, bili);
					that.imgs1 = imgarr.slice(bili, bili * 2);
					that.imgs2 = imgarr.slice(bili * 2, bili * 3);
					that.imgs3 = imgarr.slice(bili * 3);
				})
			},
			myclick(activeName, oldActiveName) {
				var that = this;
				that.imgs = [];
				that.imgs1 = [];
				that.imgs2 = [];
				that.imgs3 = [];
				//				console.log(activeName)
				var id = '';
				if(activeName == 'first') {
					id = 51
				} else if(activeName == 'second') {
					id = 52
				} else if(activeName == 'third') {
					id = 53
				} else {
					id = 54
				}
				axios.get(that.https+"/pavilion/news?c=" + id).then(function(data) {
					var data = data.data
					var bili = parseInt(data.list.length / 4);
					if(bili == 0) {
						bili = 1
					} else {
						bili = bili
					}
					var imgarr = [];
					for(var i = 0, len = data.list.length; i < len; i++) {
						imgarr.push(data.list[i])
					}
					that.imgs = imgarr.slice(0, bili);
					that.imgs1 = imgarr.slice(bili, bili * 2);
					that.imgs2 = imgarr.slice(bili * 2, bili * 3);
					that.imgs3 = imgarr.slice(bili * 3);
				})
			},
			list_xq(id){
				axios.get(this.https+'/pavilion/news-detail?id='+id).then((data)=>{
					var data = data.data
//					console.log(data);
					this.img_xq = data;
					this.show = false
				})
			},
			close(){
				this.show = true
			}
		},
		mounted: function() {
			this.getwz();
			document.addEventListener('keydown',(e)=>{
				if(e.keyCode === 27 || e.which === 27){
					this.show = true
				}
			})
		}
	}
</script>
<style type="text/css">
	/*瀑布流*/
	.list {
		width: 1200px;
		height: auto;
		position: relative;
	}
	
	.list ul {
		width: 24%;
		overflow: hidden;
		float: left;
	}
	
	.list ul li {
		width: 100%;
		margin: 10px 10px;
	}
	
	.list ul li img {
		width: 100%;
		display: block;
	}
	
	.banner {
		width: 100%;
		height: 600px;
		background: url(../../assets/img/museum_banner.jpg) no-repeat center;
		background-size: cover;
	}
	/*=========== el-tabs style ===========*/
	
	.el-tabs__header {
		height: 130px;
		border-bottom: 4px solid #f2f0f3;
		background: #f4f4f4;
		margin: 0;
	}
	
	.el-tabs__nav {
		width: 1200px;
		margin: 0 auto;
		float: none;
		height: 134px;
	}
	
	.el-tabs__item {
		height: 130px;
		width: 25%;
		font-size: 26px;
		padding: 0;
		line-height: 130px;
		color: #101010;
		text-align: center;
	}
	
	.el-tabs__item.is-active {
		color: #E51C23;
	}
	
	.el-tabs__item:hover {
		color: #101010;
	}
	
	.el-tabs__item.is-active:hover {
		color: #E51C23;
	}
	
	.el-tabs__active-bar {
		background-color: #E51C23;
		left: 5px;
		height: 4px;
	}
	
	.el-tabs__content {
		width: 1200px;
		padding: 66px 0;
		margin: 0 auto;
	}
	/*===========弹窗样式===============*/
.img_alert{
	position: fixed;
	top: 0;
	bottom:0;
	left:0;
	right: -17px;
	background: rgb(235, 235, 235);
	/*justify-content: center;*/
	z-index: 999;
	padding: 50px 0;
	overflow: auto;
}
.img_alert > i{
	display: block;
	position: absolute;
	right: 10%;
	font-size: 34px;
	font-style: normal;
	font-weight: bold;
	cursor: pointer;
}
.img_alert .img_introduce{
	position: absolute;
	width: 700px;
	margin:60px calc((100% - 700px)/2);
	background: #fff;
	padding: 60px 60px;
}
.img_alert .img_introduce h3{
	font-size: 26px;
}
.img_alert .img_introduce p{
	padding:6px 0px;
	font-size: 20px;
	line-height: 30px;
	color: #7d7d7d;
}
.img_alert .img_introduce i{
	display: block;
	width:350px;
	margin:30px auto;
	overflow: hidden;
}
</style>


