<template>
	<div class="mains">
		<my-header></my-header>
		<!-- Swiper main -->
		<div class="swiper-container main">
			<div class="swiper-wrapper mainboxs">
				<!--第一屏-->
				<div class="swiper-slide home"></div>
				<!--第二屏-->
				<div class="swiper-slide consult">

					<!--<div class="swiper-container swiper2">
						<div class="swiper-wrapper">
							<div class="swiper-slide" v-for="item in imgs">
								<img :src='item'/>
							</div>
							<!--{{items}}-->
					<!--</div>-->
					<!-- Add Pagination main-->
					<!--<div class="swiper-pagination swiper2Page"></div>-->
					<!--</div>-->
					<swiper :options="swiperOption">
						<swiper-slide v-for="(slide, index) in swiperSlides" :key="index"><img :src="slide" /></swiper-slide>
						<div class="swiper-pagination swiper2Page" slot="pagination"></div>
					</swiper>
				</div>
				<!--第三屏-->
				<div class="swiper-slide museum">
					<div class="imgs">
						<div class="mu_left">
							<div class="mu_top">
								<div class="img1 imga">
									<router-link tag="a" to="/consult">
										<!--<img src="img/home3-1.jpg"/>-->
									</router-link>
									<span>资讯</span>
								</div>
								<div class="img2 imga">
									<router-link tag="a" to="/museum">
										<!--<img src="img/home3-2.jpg"/>-->
									</router-link>
									<span>馆藏</span>
								</div>
							</div>
							<div class="mu_bottom">
								<div class="img3 imga">
									<router-link tag="a" to="/activity">
										<!--<img src="img/home3-3.jpg"/>-->
									</router-link>
									<span>活动</span>
								</div>
								<div class="img4 imga">
									<router-link tag="a" to="/wenCreative">
										<!--<img src="img/home3-4.jpg"/>-->
									</router-link>
									<span>文创</span>
								</div>
							</div>
						</div>
						<div class="mu_right">
							<div class="img5 imga">
								<router-link tag="a" to="/show_Img">
									<!--<img src="img/home3-5.jpg"/>-->
								</router-link>
								<span>展馆图集</span>
							</div>
						</div>
					</div>
				</div>
				<!--第四屏-->
				<div class="swiper-slide activity center">
					<div class="time">
						<div class="">
							<h2>开放时间</h2>
							<p>每星期二至星期日9:00-17:00（16:00停止入馆）</p>
							<p>每星期一闭馆（国家法定节假日除外）</p>
							<router-link tag="a" to="/yu_yue">预约参观</router-link>
						</div>
					</div>
					<div class="member">
						<div class="">
							<h2>会员权益</h2>
							<p>会员即可免费参观展厅</p>
							<p>成为心愿汇后即可享受免费参观展厅等多项权益</p>
							<router-link tag="a" to="/log_in">成为会员</router-link>
						</div>
					</div>
				</div>
			</div>
			<!-- Add Pagination main-->
			<div class="swiper-pagination mainPage"></div>
		</div>
		<!--right sidebar start-->
		<div class="sidebar">
			<ul class="center">
				<li class="center">
					<router-link tag="a" to="/yu_yue"><i class="iconfont iconyuyue"></i>预约</router-link>
				</li>
				<li class="center wx">
		        	<i class="iconfont iconweixin"></i>微信
		        	<p class="wx_ma center">
		        		<img src="@/assets/img/wx_二维码.jpg"/>
		        		<span>扫码关注佛缘之路</span>
		        	</p>
		        </li>
		        <li class="center shoop">
		        	<i class="iconfont iconshangcheng"></i>佛缘商城
		        	<p class="shoop_ma center">
		        		<img src="@/assets/img/wx_二维码.jpg"/>
		        		<span>扫码关注商城</span>
		        	</p>
		        </li>
			</ul>
		</div>
		<!--right sidebar end-->

		<!--footer start-->
		<div class="footer center">
			<div class="foot_left">
				<a class="button" id="button"></a>
			</div>
			<div class="foot_right">
				<ul class="center">
					<li><i class="iconfont icondizhi"></i>地址：北京xxx</li>
					<li><i class="iconfont iconshouji"></i>电话：12345</li>
					<li>佛缘之路版权所有</li>
				</ul>
			</div>
		</div>
		<!--footer end-->

		<!--footer alert start-->
		<div class="foot_alert">
			<ul>
				<li class="center">友情链接：
					<a>凤凰佛教 </a>
					<a>凤凰佛教 </a>
					<a>凤凰佛教 </a>
				</li>
				<li class="center">关于我们：
					<a>佛缘之路 </a>
					<a>佛缘之路 </a>
					<a>佛缘之路 </a>
				</li>
			</ul>
		</div>
		<!--footer alert end-->
	</div>
</template>

<script>
	import { swiper, swiperSlide } from 'vue-awesome-swiper'
	import axios from 'axios'
	export default {
		name: 'mains',
		components: {
			swiper,
			swiperSlide
		},
		data() {
			return {
				swiperOption: {
					pagination: {
						el: '.swiper2Page'
					}
				},
				swiperSlides: [],
				banners:'',
				https:this.url.https,
				img_http:this.img_url.img_http
			}
		},
		mounted() {
			var that = this;
			axios.get(that.https+'/pavilion/banner').then(function(data) {
//				console.log(data.data);
				var data = data.data;
				that.banners = that.img_http + data.banner;
				var imgfilterarr = [];
				data.imgs.filter((item) => {
					var items = that.img_http + item;
					imgfilterarr.push(items)
				})
				that.swiperSlides = imgfilterarr;
				//banner
				var banner = document.getElementsByClassName('home')[0];
				banner.style.background = "url("+that.banners+") no-repeat center";
				banner.style.backgroundSize = "cover";
			});
			init();
			//main init
			function init() {
				var swiper = new Swiper('.main', {
					pagination: '.mainPage',
					paginationClickable: true,
					direction: 'vertical',
					mousewheelControl: true,
					keyboardControl: true,
					onSlideChangeStart: function(swiper) {

					}
				});
			}
			
			/*================底部弹出框=================*/
			var foot_button = document.getElementById('button'),
			foot_alert = document.getElementsByClassName('foot_alert')[0];
			foot_button.addEventListener('mouseover', function() {
				foot_alert.style.bottom = "86px";
				foot_button.style.borderBottomColor='rgb(229, 28, 35)';
			});
			foot_alert.addEventListener('mouseover', function() {
				foot_alert.style.bottom = "86px";
				foot_button.style.borderBottomColor='rgb(229, 28, 35)';
			})
			foot_alert.addEventListener('mouseout', function() {
				foot_alert.style.bottom = "-100px";
				foot_button.style.borderBottomColor='#EEEEEE';
			});
		}
	};
</script>

<style>
	@import '../assets/css/swiper.min.css';
	@import '../assets/css/index.css';
	.mains .consult {
		padding-top: 0;
	}
	
	.mains .swiper-container {
		width: 100%;
		height: 100%;
	}
	
	.mains .swiper-slide {
		text-align: center;
		font-size: 18px;
		background: #fff;
		/* Center slide text vertically */
		display: -webkit-box;
		display: -ms-flexbox;
		display: -webkit-flex;
		display: flex;
		-webkit-box-pack: center;
		-ms-flex-pack: center;
		-webkit-justify-content: center;
		justify-content: center;
		-webkit-box-align: center;
		-ms-flex-align: center;
		-webkit-align-items: center;
		align-items: center;
		padding-top: 110px;
		height: calc(100% - 110px) !important;
	}
	
	.mains .swiper-pagination-bullet {
		background: #b5b5b5;
		opacity: 1;
		width: 10px;
		height: 100px;
		border-radius: 0;
	}
	
	.mains .swiper-pagination-bullet-active {
		background: #1b1b1b;
	}
	
	.mains .consult .swiper-container {
		margin-bottom: 86px;
		height: calc(100% - 86px);
	}
	
	.mains .consult .swiper-slide {
		padding-top: 0;
		height: 100% !important;
	}
</style>


