<template>
	<div class="yu_yue">
		<my-header style="position: relative;"></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--main开始-->
		<div class="mainbox">
			<div class="box_one">
				<h2>预约须知</h2>
				<p>观众朋友您好，欢迎参观佛缘之路展厅</p>
				<p>我馆开放时间为每周二至周日9：00~17：00（16：00停止入馆），周一（法定节假日除外）闭馆（特殊情况请参照开放公告）。</p>
				<p>我馆为观众提供网上预约服务，每张有效身份证件每天限定预约一次，最多实名预约3人。</p>
				<p>预约成功后，凭预约短信及有效证件在入口处予工作人员核查，核实后可直接从绿色通道入馆。若未在预约时段到馆，视为无效，谢谢合作。</p>
				<p>友情提醒各位预约最后一个时间段的朋友（15：00~15：40）务必于16：00之前入馆，16：00后博物馆将不再允许观众入场。</p>
			</div>
			<div class="box_two">
				<p>微信扫描下方二维码即可在线预约</p>
				<i>
						<img src="../assets/img/erweima.jpg"/>
					</i>
			</div>
		</div>
		<!--main结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
	</div>
</template>
<script type="text/javascript">
	export default {
		name: 'yu_yue'
	}
</script>
<style type="text/css" scoped>
	.main {
		width: 100%;
	}
	/*================banner style=============*/
	
	.banner {
		width: 100%;
		height: 600px;
		background: url(../assets/img/yu_yue_banner.jpg) no-repeat center;
		background-size: cover;
	}
	/*=================main style==============*/
	
	.mainbox {
		width: 1200px;
		margin: 0 auto 200px;
	}
	
	.mainbox .box_one {
		padding: 104px 50px 27px;
		border-bottom: 1px dashed #dcdcdc;
		text-align: center;
	}
	
	.mainbox .box_one h2 {
		color: #ff0000;
		margin-bottom: 17px;
	}
	
	.mainbox p {
		color: #7c827d;
		line-height: 34px;
		text-align: left;
		font-size: 20px;
	}
	
	.mainbox .box_two {
		padding: 72px 0;
		text-align: center;
	}
	
	.mainbox .box_two p {
		text-align: center;
		margin-bottom: 28px;
	}
	
	.mainbox .box_two i {
		width: 279px;
		height: 279px;
		display: inline-block;
		overflow: hidden;
	}
	
	.mainbox .box_two i img {
		display: block;
		width: 99%;
	}
</style>


