<template>
	<div class="show_img">
		<my-header style="position: relative;"></my-header>
		<!--banner start-->
		<div class="banner"></div>
		<!--banner end-->
		<!--选项卡开始-->
		<el-tabs v-model="activeName">
			<el-tab-pane label="展馆图集" name="first" class="showImg">
				<div class="list clearfix">
					<ul>
						<li v-for="(item,index) in imgs"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs1" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs2" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
					<ul>
						<li v-for="(item,index) in imgs3" data-scroll-reveal="enter"><img :src="img_http+item.img_url" alt=""></li>
					</ul>
				</div>
			</el-tab-pane>
		</el-tabs>
		<!--选项卡结束-->
		<!--尾部开始-->
		<my-footer style="position: relative;"></my-footer>
		<!--尾部结束-->
	</div>
</template>
<script type="text/javascript">
	import axios from 'axios'
	export default {
		name: 'show_img',
		data(){
			return{
				activeName: 'first',
				imgs: [],
				imgs1: [],
				imgs2: [],
				imgs3: [],
				https:this.url.https,
				img_http:this.img_url.img_http
			}
		},
		mounted() {
			var that = this;
				that.imgs = [];
				that.imgs1 = [];
				that.imgs2 = [];
				that.imgs3 = [];
				axios.get(that.https+"/pavilion/news?c=55").then(function(data) {
					console.log(data);
					var data = data.data;
					var bili = parseInt(data.list.length / 4);
					if(bili == 0) {
						bili = 1
					} else {
						bili = bili
					}
					var imgarr = [];
					for(var i = 0, len = data.list.length; i < len; i++) {
						imgarr.push(data.list[i])
					}
					that.imgs = imgarr.slice(0, bili);
					that.imgs1 = imgarr.slice(bili, bili * 2);
					that.imgs2 = imgarr.slice(bili * 2, bili * 3);
					that.imgs3 = imgarr.slice(bili * 3);
				})
		}
	}
</script>
<style>
	/*瀑布流*/
	.list {
		width: 1200px;
		height: auto;
		position: relative;
	}
	
	.list ul {
		width: 24%;
		overflow: hidden;
		float: left;
	}
	
	.list ul li {
		width: 100%;
		margin: 10px 10px;
	}
	
	.list ul li img {
		width: 100%;
		display: block;
	}
	
	.banner {
		width: 100%;
		height: 600px;
		background: url(../assets/img/museum_banner.jpg) no-repeat center;
		background-size: cover;
	}
	/*=========== el-tabs style ===========*/
	
	.el-tabs__header {
		height: 130px;
		border-bottom: 4px solid #f2f0f3;
		background: #f4f4f4;
		margin: 0;
	}
	
	.el-tabs__nav {
		width: 1200px;
		margin: 0 auto;
		float: none;
		height: 134px;
		display: flex;
		justify-content: center;
	}
	
	.el-tabs__item {
		height: 130px;
		width: 300px;
		font-size: 26px;
		padding: 0;
		line-height: 130px;
		color: #101010;
		text-align: center;
	}
	
	.el-tabs__item.is-active {
		color: #E51C23;
	}
	
	.el-tabs__item:hover {
		color: #101010;
	}
	
	.el-tabs__item.is-active:hover {
		color: #E51C23;
	}
	
	.el-tabs__active-bar {
		background-color: #E51C23;
		left: 5px;
		height: 4px;
	}
	
	.el-tabs__content {
		width: 1200px;
		padding: 66px 0;
		margin: 0 auto;
	}
	.show_img .el-tabs__active-bar{
		transform: translateX(150%) !important;
	}
</style>


