					<h5>1)个人预约</h5>
					<p>请至少提前一天登录博物馆官方网站个人预约通道进行参观预约。每张有效身份证件每天限定预约一次，最多实名预约3人。</p>
					<p>本馆举办的特别展览将酌情实施售票参观制度，面向学生团体、高领老人、现役军人、离休干部、残障人士等特殊群体的各类优惠政策继续有效。</p>
					<p>特别展览的售票不影响观众免费入馆参观本馆其他基本陈列。</p>
