<script type="text/javascript">
	import axios from 'axios'
	export default {
		name: 'consult',
		data() {
			return {
				activeName: 'first',
				pages: 5,
				currentPage:1,
				show: true,
				dynamic_list:[],
				id:null,
				scrolltop:720,
				https:this.url.https,
				img_http:this.img_url.img_http
			}
		},
		mounted() {
			var that = this;
				axios.get(that.https+'/pavilion/news', {
					page: that.currentPage
				}).then(
				function(data) {
					var data = data.data;
//					console.log(data);
					that.dynamic_list = data.list;
//					that.pages = data.count;
					that.show = true;
				});
		},
		methods:{
			list_xq(id){
				var that = this;
				axios.get(that.https+'/pavilion/news-detail?id=' + id).then(
				function(data) {
					var data = data.data;
//					console.log(data);
					that.show = false;
					that.list = data;
				});
			},
			myclick(activeName, oldActiveName){
				this.show = true;
				document.documentElement.scrollTop = this.scrolltop;
			},
			currentChange(val){
//				console.log(val);
				var that = this;
				that.currentPage = val;
				axios.get(that.https+'/pavilion/news', {
					params:{
						page: val
					}
				}).then(
				function(data) {
					var data = data.data;
//					console.log(data)
					that.dynamic_list = data.list;
//					that.pages = data.count;
				});
			}
		}
	}
</script>
<style>
	/*================banner style=============*/
	.banner {
		width: 100%;
		height: 600px;
		background: url("../assets/img/consult_banner.jpg") no-repeat center;
		background-size: cover;
	}
	/*=========== el-tabs style ===========*/
	.el-tabs__header {
		height: 130px;
		border-bottom: 4px solid #f2f0f3;
		background: #f4f4f4;
		margin: 0;
	}
	
	.el-tabs__nav {
		width: 1200px;
		margin: 0 auto;
		float: none;
		height: 134px;
	}
	
	.el-tabs__item {
		height: 130px;
		width: 25%;
		font-size: 26px;
		padding: 0;
		line-height: 130px;
		color: #101010;
		text-align: center;
	}
	
	.el-tabs__item.is-active {
		color: #E51C23;
	}
	
	.el-tabs__item:hover {
		color: #101010;
	}
	.el-tabs__item.is-active:hover{
		color: #E51C23;
	}
	
	.el-tabs__active-bar {
		background-color: #E51C23;
		left: 5px;
		height: 4px;
	}
	
	.el-tabs__content {
		width: 1200px;
		padding: 66px 0;
		margin: 0 auto;
	}
	/*场馆介绍*/
	
	.introduce {
		padding-top: 4px;
	}
	
	.introduce>div {
		padding: 42px 0;
	}
	
	.introduce>div p {
		margin: 0 20px;
		font-size: 24px;
		color: #101010;
		line-height: 42px;
	}
	
	.introduce>div i {
		display: block;
		width: 543px;
		height: 327px;
		margin: 0 auto;
		overflow: hidden;
	}
	
	.introduce>div.center {
		align-items: flex-start;
	}
	
	.introduce>div:nth-of-type(2n) {
		flex-direction: row-reverse;
	}
	/*最新动态*/
	
	.dynamic .news_list li {
		height: 120px;
		line-height: 120px;
		text-align: center;
		border-bottom: 1px solid #e1e1e1;
		font-size: 24px;
		display: flex;
		justify-content: center;
		color: #b0b0b0;
		cursor: pointer;
	}
	
	.dynamic .news_list li:nth-last-of-type {
		border-bottom: 0 solid #e1e1e1;
	}
	
	.dynamic .news_list li time {
		margin-right: 10px;
		font-size: 18px;
	}
	
	.dynamic .news_list li time span {
		font-size: 28px;
		font-weight: bold;
		color: #101010;
	}
	
	.dynamic .news_list li p {
		width: 70%;
		display: inline-block;
	}
	
	.dynamic .news_list li:hover p {
		color: #f5ac37;
	}
	
	.el-pagination {
		margin-top: 20px;
	}
	
	.consultpag.center {
		justify-content: center;
	}
	
	.el-pagination button,
	.el-pagination li {
		width: 64px;
		height: 45px;
		line-height: 45px;
		text-align: center;
		margin: 0 5px;
		background: #e4e4e4;
		cursor: pointer;
		font-size: 18px;
	}
	
	.el-pager li.btn-quicknext,
	.el-pager li.btn-quickprev {
		line-height: 45px;
	}
	
	.el-pagination.is-background .el-pager li:not(.disabled).active {
		background: #f5ac37;
		color: #fff;
	}
	
	.el-pagination.is-background .el-pager li:not(.disabled):hover {
		background: #f5ac37;
		color: #fff;
	}
	/*动态详情*/
	
	.list_xq {
		width: 100%;
		position: relative;
	}
	
	.list_xq h2 {
		line-height: 40px;
	}
	
	.list_xq p {
		line-height: 25px;
		color: #888c90;
	}
	
	.list_xq article {
		margin: 20px 0;
		line-height: 25px;
	}
	
	.list_xq article img {
		display: block;
		width: 70%;
		margin: 20px auto;
	}
	.goback{
		position: absolute;
		right: 0;
	}
	.goback:hover{
		color: #F5AC37;
	}
	/*开放信息*/
	
	.dispark_mes {
		color: #101010;
		padding-top: 15px;
	}
	
	.dispark_mes h4,
	.dispark_mes h5 {
		line-height: 58px;
		font-size: 20px;
	}
	
	.dispark_mes p {
		line-height: 30px;
		font-size: 18px;
		margin: 10px 0;
	}
	/*大事记*/
	
	.cntl {
		position: relative;
		width: 100%;
		overflow: hidden;
	}
	
	.cntl-center {
		left: 0;
		right: 0;
		margin-left: auto;
		margin-right: auto;
	}
	
	.cntl-bar {
		position: absolute;
		width: 10px;
		top: 0;
		bottom: 0;
		background-color: #ccc;
		box-shadow: inset 0px 0px 7px -2px #000;
	}
	
	.cntl-bar-fill {
		background-color: #807a8d;
		position: absolute;
		left: 0;
		right: 0;
		top: 0;
		height: 0;
	}
	
	.cntl-state {
		position: relative;
		width: 100%;
		min-height: 200px;
		margin-bottom: 50px;
	}
	
	.cntl-state::after {
		display: block;
		content: ' ';
		clear: both;
	}
	
	.cntl-icon {
		border-radius: 50%;
		width: 54px;
		height: 54px;
		background-color: #00313C;
		border: solid 3px #009ABB;
		box-shadow: 0px 0px 19px -9px #000;
		position: absolute;
		top: 0;
		text-align: center;
		line-height: 80px;
		font-size: 40px;
		color: #fff;
	}
	
	.one {
		background-color: #f39a99;
		border: solid 13px #d75357;
	}
	
	.two {
		background-color: #f9e1ab;
		border: solid 13px #f5b64a;
	}
	
	.three {
		background-color: #4fc0cd;
		border: solid 13px #2d96a2;
	}
	
	.four {
		background-color: #5a9fd5;
		border: solid 13px #2d77b3;
	}
	
	.five {
		background-color: #9c69a9;
		border: solid 13px #7d4792;
	}
	
	.cntl-content {
		width: 40%;
		padding: 2%;
		background-color: rgba(238, 238, 238, 0.25);
		border-radius: 8px;
		border-bottom: solid 3px #009ABB;
		float: left;
		opacity: 1;
		position: relative;
		/*margin-left:-40%;*/
	}
	
	.cntl-state:nth-child(2n+2) .cntl-content {
		float: right;
		/*margin-right:-40%;*/
	}
	
	.cntl-image {
		opacity: 1;
		width: 40%;
		padding: 2%;
	}
	
	.cntl-state:nth-child(2n+1) .cntl-image {
		float: right;
	}
	
	.cntl-image img {
		width: 100%;
	}
	
	.cntl-content h4 {
		font-size: 20px;
		font-weight: normal;
		margin-bottom: 10px;
	}
	/*
animations
*/
	
	.cntl-bar-fill,
	.cntl-content,
	.cntl-image {
		-webkit-transition: all 500ms ease;
		-moz-transition: all 500ms ease;
		-o-transition: all 500ms ease;
		-ms-transition: all 500ms ease;
		transition: all 500ms ease;
	}
	
	.cntl-state:nth-child(2n+2).cntl-animate .cntl-content {
		margin-right: 0%;
	}
	
	.cntl-animate .cntl-content {
		opacity: 1;
		margin-left: 0%;
	}
	
	.cntl-animate .cntl-image {
		opacity: 1;
	}
	/* mobile support */
	
	@media (max-width: 600px) {
		.cntl-bar {
			left: auto;
			right: 37px;
		}
		.cntl-content {
			width: 80%;
			float: none;
		}
		.cntl-state:nth-child(2n+2) .cntl-content {
			margi-right: 0%;
			float: none;
		}
		.cntl-image {
			width: 80%;
		}
		.cntl-state:nth-child(2n+1) .cntl-image {
			float: none;
		}
		.animate .cntl-content {
			margin-left: 2%;
		}
		.cntl-icon {
			left: auto;
			right: 0;
		}
	}
</style>


